
import Scroller from 'vux/src/components/scroller/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import dateFormat from 'vux/src/tools/date/format.js'

import {detail} from '../config'
import VScroll from '../components/VScroll'
export default {
  head: {
    title: {
      inner: ''
    }
  },
  components: {
    Scroller,
    Group,
    Cell,
    VScroll
  },
  data () {
    return {
      balance: 0,
      form: {
        userId: '',
        type: 1,
        limit: 10,
        pageIndex: 0
      },
      list: []
    }
  },
  mounted () {
    this.balance = this.$localStorage.get('balance')
    this.form.userId = JSON.parse(this.$localStorage.get('userInfo')).userId
    this.getList(() => {}, null)
  },
  methods: {
    onRefresh (done) {
      this.form.pageIndex = 0
      this.getList(done, 1)
    },
    onInfinite (done) {
      this.form.pageIndex ++
      this.getList(done, 0)
    },
    getList (done, status) {
      const This = this
      this.$http({
        method: 'jsonp',
        url: detail,
        jsonp: 'callback',
        jsonpCallback: 'json',
        params: this.form,
        before: () => {
          if (status) {
            this.list = []
          }
        }
      })
      .then(res => {
        console.log(res)
        res.body.data.scoreList.forEach(el => {
          This.list.push(el)
        })
        if (res.body.data.scoreList.length < This.form.limit) {
          this.$el.querySelector('.load-more').innerHTML = ''
        }
        done()
        for (const i in this.list) {
          this.list[i].createTime = dateFormat(this.list[i].createTime)
        }
      })
    }
  }
}



